<template>
  <div class="tongxunlu">

   	<div class="scroo" ref='scroo'>
   		<div>
   			<ul ref='ulheigth'  @touchmove="tamove">
   				<li v-for="(i,index) in data" class="list-group" ref='liheigth'>
   					<h2 class="list-group-title" ref="zhentitle">{{i.title}}</h2>
   					<ul>
   						<li @click='sellgeshou(it)' class="list-group-item border-1px" v-for='it in i.items'>
   							<img v-lazy="it.avatar" class="avater">
   							<span class="name">{{it.name}}</span>
   						</li>
   					</ul>
   				</li>
   			</ul>
   		</div>
   	</div>
    <div class="list-shortcut" >
      <ul >
        <li class="item" :class="{'avtive':i.title===currentIndex}"  v-for="(i,index) in data" @touchstart="onShortstars(index)">
          {{i.title=='热门' ? '热' : i.title}}
        <!--   {{currentIndex}} -->
        </li>
      </ul>
    </div>
    <div v-show='isshow11'  class="dingwei" ref='jiatitle'>
      {{currentIndex}}
  </div>
    <div v-show="!data.length" class="landing">
      <lang></lang>
    </div>
  </div>
</template>

<script>

import bcsool from 'better-scroll'
import lang from '../../base/langing/hello'
export default {
  name: 'tongxunlu',
  data () {
    return {
     listHeight : [],
     scrollY : '',
     noscrollY : '',
     isbli : true,
     isshow11 : 'true',
     diff : -1
    }

  },
  props : {
  	data : {}
  },
  components : {
    lang
  },
  created() {

  },
  mounted() {


  },
  methods : {
    sellgeshou(i){
      // console.log(i)
      this.$emit('getgeshou',i)
    },
    tamove() {
      // console.log(this.data)
       this.soll.on("scroll",(pos)=>{
        this.scrollY = Math.abs(Math.round(pos.y))
         this.noscrollY = Math.round(pos.y)
         // console.log(this.noscrollY)
        // console.log(this.scrollY)
        })
       // console.log(this.$refs.jiatitle.offsetTop)
       // if(this.isbli){
       //  // console.log(this.scrollY)
       //  this.listHeight.forEach((i)=>{
       //    if(Math.abs(i-this.scrollY))
       //  })
       //  this.isbli = false
       // }

    },
  	_gundong() {
  		this.soll = new bcsool(this.$refs.scroo,{
          probeType: 3,
          click: true
      })
  	},
    onShortstars(ind) {
      // console.log('li')
       let lilist = this.$refs.ulheigth.getElementsByClassName('list-group');
     this.soll.scrollToElement(lilist[ind],300)
    },
    _liheigth() {
        let lilist = this.$refs.ulheigth.getElementsByClassName('list-group');
       // console.log(lilist)
        let height = 0;
        this.listHeight.push(height);
        for(let i = 0; i < lilist.length; i++){
          let item = lilist[i]
          height += item.clientHeight;
          // console.log(height)
          this.listHeight.push(height);
          // console.log(this.listHeight)
        }
        // console.log(this.listHeight)
        //

    },

  },
  computed : {
     currentIndex() {
      for(let i =0; i<this.listHeight.length;i++){
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i+1];
        // console.log(height1,height2)
        if(this.scrollY>=height1 && this.scrollY<height2){
          // console.log(height1,height2)
          // console.log('我在'+i)
          this.diff =height2-this.scrollY
          // console.log(this.diff)
          return this.data[i].title;
        }
      }
      return ;
    },
    // isshow1() {
    //   for(let i =0; i<listHeight.length;i++){
    //     return
    // }
    //   }
  },
  watch : {
  	data() {
  		setTimeout(()=>{
  			this.$nextTick(()=>{
         this. _liheigth()

  			// console.log(1111)
			this._gundong()

  	})
  		},1)
  		// this.soll.refresh() 2.35

  	},
    noscrollY(n){

      if(n>0){
        this.isshow11 = false

      }else if(n<0){
        //  this.listHeight.forEach((i,index)=>{

        //   if(Math.abs(i-this.scrollY)<28){
        //      // this.isshow11 = false
        //      this.$refs.jiatitle.style.color = 'red'
        //      console.log('我消失了',i-this.scrollY)
        //      // this.$refs.showtitle.style.top =40 - Math.abs(i-this.scrollY) + 'px'
        //      // console.log(this.noscrollY)
        //      // this.$nextTick(()=>{
        //      //    console.log(this.$refs)
        //      // })

        //      // this.$refs.jiatitle.style.transfrom = 'translate3d(0,20px,0)'
        //   }else if((Math.abs(i-this.scrollY)>28)){
        //        console.log('我来了',i-this.scrollY)
        //        // this.$refs.showtitle.style.top =40
        //         this.$refs.jiatitle.style.color = '#fff'
        //   }
        // })
       this.isshow11 = true
      }
    },
    diff(new1) {
      // console.log(new1)
        let fietop = (new1>0 && new1 < 28) ? new1-28 : 0
        fietop = fietop +'px'
        // console.log(new1,fietop)
        this.$refs.jiatitle.style.transform =`translate3d(0,${fietop},0)`
        // this.$refs.jiatitle.style.height = fietop+'px'
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../commnt/stylus/index"
.dingwei{
  position: fixed;
  width: 100%;
  top : 40px;
  height :28px;
  line-height :28px;
  padding-left :20px;
  background :#eee;
}
.landing{
   position: fixed;
  top : 50%;
  width: 100%;
  height: 100%;
  background : #fff;
}
.list-shortcut{
  position :absolute;
  z-index :30;
  right :0;
  top : 52%;
  transform :translateY(-50%);
  width:30px;
  // padding:20px 0;
  border-radius :50%;
  text-align :center;
  // background :#fff;
  font-size 0px
}
.list-shortcut .item{
  padding:2px;
  line-height :1;
  color : #666;
  font-size 14px;
}
.list-shortcut .avtive{
   color : #ccc;
   font-size 18px
}
.tongxunlu{
  height: 100%;
  width: 100%;
  background: #fff;
  overflow :hidden;
}

.scroo{
  position : relative;
  height :100%;
  overflow :hidden;
}
.list-group:last-child{
  padding-bottom :46px;
}
.list-group-title{
  height :28px;
  line-height :28px;
  padding-left :20px;
  background :#eee;
  // position: absolute;
  // z-index 100;
  // width :100%;
  // overflow hidden
}
.list-group-item{
  margin-left :30px;
  height: 30px;
  overflow: hidden;
  padding: 5px 0 5px 0px
  border-1px(#eee)

}

.list-group-item img{
  border-radius :50%;
  height: 100%;
  vertical-align:middle;
}
.list-group-item .name{
  // line-height: 30px;
  font-size: 16px;
}
</style>



